<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js";
  import Container from "../shared/Container.svelte";
  import Modal from "../shared/Modal.svelte";
  export let quiz;
  const dispatch = new createEventDispatcher();
  let students = [];
  let student;
  let answers = [];
  let multipleChoices = [];
  let timeoutModal;
  let counter;
  let isStudent;
  let counterInterval;

  onMount(async () => {
    let i = 0;
    quiz.items.forEach((item) => {
      if (item.type === "Multiple choices") {
        multipleChoices[i] = [...item.incorrectAnswers, item.answer];
        multipleChoices[i].sort(() => Math.random() - 0.5);
        i++;
      }
    });
    const tmp = quiz;
    quiz = undefined;
    quiz = tmp;
    students = await fetch("/api/v1/student", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());

    if (quiz?.timeLimit != 0) {
      counter = quiz?.timeLimit;
      counterInterval = setInterval(() => {
        counter--;
      }, 1000);

      setTimeout(async () => {
        await checkQuiz();
      }, quiz?.timeLimit * 1000);
    }

    let userInfo = getCurrentUserInfo();

    if (userInfo?.user?.role == "Student") {
      student = students.filter(
        (s) => s.userInfo.user.id == userInfo.user.id
      )[0];
      isStudent = true;
    }
  });

  async function checkQuiz() {
    await fetch("/api/v1/quizResult/check", {
      headers: getDefaultHeaders(),
      method: "POST",
      body: JSON.stringify({
        quiz: {
          id: quiz.id,
        },
        student: {
          id: student.id,
        },
        answers,
      }),
    }).then(async () => {
      await dispatch("refreshQuizResults");
      clearInterval(counterInterval);
      counter = 0;
      await timeoutModal.toggle();
      setTimeout(() => {
        document.location.hash = "quizResults";
        document.location.reload();
      }, 1000);
    });
  }

  function secondsToHms(d) {
    d = Number(d);
    var h = Math.floor(d / 3600);
    var m = Math.floor((d % 3600) / 60);
    var s = Math.floor((d % 3600) % 60);

    var hDisplay = h > 0 ? h + (h == 1 ? " hour, " : " hours, ") : "";
    var mDisplay = m > 0 ? m + (m == 1 ? " minute, " : " minutes, ") : "";
    var sDisplay = s > 0 ? s + (s == 1 ? " second" : " seconds") : "";
    return hDisplay + mDisplay + sDisplay;
  }
</script>

{#if counter != 0}
  Timer: {secondsToHms(counter)} left
{/if}
{quiz.title} ({quiz.subject.name}) by {quiz.teacher.userInfo.user.name}<br />
{#if !isStudent}
  Student:
  <div class="mb-3 row">
    <select class="form-control" bind:value={student}>
      <option value="">Select a Student</option>
      {#each students as student}
        <option value={student}>{student.userInfo.user.name}</option>
      {/each}
    </select>
  </div>
  <br />
{/if}
{#each quiz.items as item, i}
  {i + 1}.&nbsp;{item.question}<br />
  {#if item.type === "Multiple choices" && multipleChoices[i]?.length > 0}
    <div style="display: flex; flex-wrap: wrap;" class="m-3">
      {#each multipleChoices[i] as answer}
        &nbsp;&nbsp;
        <div class="mb-3 row">
          <label class="form-check-label m-3">
            <input
              class="form-check-input"
              bind:group={answers[i]}
              type="radio"
              value={answer}
            />{answer}</label
          >
        </div>
      {/each}
    </div>
  {:else if item.type === "True or False"}
    <div style="display: flex; flex-wrap: wrap;" class="m-3">
      &nbsp;&nbsp;
      <div class="mb-3 row">
        <label class="form-check-label m-3">
          <input
            class="form-check-input"
            bind:group={answers[i]}
            type="radio"
            value="true"
          />True</label
        >
      </div>
      &nbsp;&nbsp;
      <div class="mb-3 row">
        <label class="form-check-label m-3">
          <input
            class="form-check-input"
            bind:group={answers[i]}
            type="radio"
            value="false"
          />False</label
        >
      </div>
    </div>
  {:else if item.type === "Fill in the blank"}
    &nbsp;&nbsp;Answer:<input
      class="form-control"
      id={`${i}Input`}
      bind:value={answers[i]}
    />
  {/if}
{/each}
<br />
<div class="modal-footer ">
  <button class="btn btn-success" on:click={async () => await checkQuiz()}
    >Submit</button
  >
</div>
<Modal bind:this={timeoutModal}
  ><span slot="body">Time's up. Submitting answers...</span></Modal
>
