<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js";
  const dispatch = new createEventDispatcher();

  export let student;
  let sections = [];
  let section;
  let username;
  let password;
  let studentName;
  let birthday;
  let gender;
  let address;
  let contactNumber;
  let guardianName;
  let gurdianContactNumber;

  onMount(async () => {
    sections = await fetch("/api/v1/section", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
    username = student.userInfo.user.username;
    studentName = student.userInfo.user.name;
    birthday = student.userInfo.birthday;
    gender = student.userInfo.gender;
    address = student.userInfo.address;
    section = student.section;
    guardianName = student.guardian.name;
    contactNumber = student.userInfo.contactNumber;
    gurdianContactNumber = student.guardian.contactNumber;
  });

  async function updateStudents() {
    await fetch("/api/v1/student", {
      headers: getDefaultHeaders(),
      method: "PUT",
      body: JSON.stringify({
        id: student.id,
        userInfo: {
          id: student.userInfo.id,
          user: {
            id: student.userInfo.user.id,
            username,
            password,
            name: studentName,
          },
          birthday: new Date(birthday).toISOString(),
          gender,
          address,
          contactNumber,
        },
        section: {
          id: section.id,
        },
        guardian: {
          name: guardianName,
          contactNumber: gurdianContactNumber,
        },
      }),
    }).then(async () => await dispatch("refreshStudents"));
  }
</script>

<div class="mb-3 row">
  <label
    >Username: <input
      class="form-control"
      type="text"
      bind:value={username}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Password: <input
      class="form-control"
      type="password"
      bind:value={password}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Name: <input
      class="form-control"
      type="text"
      bind:value={studentName}
    /></label
  >
</div>
<div class="mb-3 row">
  <label>
    Section:
    <select class="form-control" bind:value={section}>
      <option value="">Select a Section</option>
      {#each sections as section}
        <option value={section}>{section.name}</option>
      {/each}
    </select>
  </label>
</div>
<div class="mb-3 row">
  <label
    >Birthday: <input
      class="form-control"
      type="datetime-local"
      bind:value={birthday}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Gender:
    <select class="form-control" bind:value={gender}>
      <option value="">Select gender</option>
      <option>Male</option>
      <option>Female</option>
    </select>
  </label>
</div>
<div class="mb-3 row">
  <label
    >Address: <input
      class="form-control"
      type="text"
      bind:value={address}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Contact Number: <input
      class="form-control"
      type="tel"
      placeholder="+639123456789"
      pattern="\+639[0-9]{9}"
      bind:value={contactNumber}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Guardian Name: <input
      class="form-control"
      type="text"
      bind:value={guardianName}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Guardian Contact number: <input
      class="form-control"
      type="tel"
      placeholder="+639123456789"
      pattern="\+639[0-9]{9}"
      bind:value={gurdianContactNumber}
    /></label
  >
</div>
<div class="modal-footer ">
  <button class="btn btn-warning" on:click={async () => await updateStudents()}
    >Submit</button
  >
</div>
