<script>
  import { onMount, createEventDispatcher } from "svelte";
  import Cookies from "js-cookie";
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js";
  const dispatch = new createEventDispatcher();

  let teachers = [];
  let subjects = [];
  let teacher;
  let subject;
  let title;
  let files = [];
  let dateOfSubmission;
  const currentUserInfo = getCurrentUserInfo();

  onMount(async () => {
    teachers = await fetch("/api/v1/teacher", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
    subjects = await fetch("/api/v1/subject", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());

    if (currentUserInfo.user.role === "Teacher") {
      teachers.forEach((t) => {
        if (t.userInfo.user.id === currentUserInfo.user.id) {
          teacher = t;
        }
      });
    }
  });

  async function uploadActivity() {
    const formData = new FormData();
    formData.append("title", title);
    formData.append("subjectId", subject.id);
    formData.append("teacherId", teacher.id);
    formData.append("file", files[0]);
    formData.append(
      "dateOfSubmission",
      new Date(dateOfSubmission).toISOString()
    );
    await fetch("/api/v1/activity/uploadFile", {
      headers: {
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "POST",
      body: formData,
    }).then(async () => await dispatch("refreshActivities"));
  }
</script>

<div class="mb-3 row">
  <label
    >Title: <input class="form-control" type="text" bind:value={title} /></label
  >
</div>
{#if currentUserInfo.user?.role == "Admin"}
  <div class="mb-3 row">
    <label>
      Teacher:
      <select class="form-control" bind:value={teacher}>
        <option value="">Select a Teacher</option>
        {#each teachers as teacher}
          <option value={teacher}>{teacher.userInfo.user.name}</option>
        {/each}
      </select>
    </label>
  </div>
{/if}
<div class="mb-3 row">
  <label>
    Subject:
    <select class="form-control" bind:value={subject}>
      <option value="">Select a Subject</option>
      {#each subjects as subject}
        <option value={subject}>{subject.name}</option>
      {/each}
    </select>
  </label>
</div>
<div class="mb-3 row">
  <label
    >Date of Submission: <input
      class="form-control"
      type="datetime-local"
      bind:value={dateOfSubmission}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >File: <input
      class="form-control"
      type="file"
      name="file"
      bind:files
    /></label
  >
</div>
<div class="modal-footer ">
  <button class="btn btn-success" on:click={async () => await uploadActivity()}
    >Submit</button
  >
</div>
