<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getDefaultHeaders } from "../utils.js";
  const dispatch = new createEventDispatcher();

  export let quizResult;
  let students = [];
  let quiz;
  let student;
  let answers = [];
  let multipleChoices = [];
  let comment;

  onMount(async () => {
    quiz = quizResult.quiz;
    let i = 0;
    quiz.items.forEach((item) => {
      if (item.type === "Multiple choices") {
        multipleChoices[i] = [...item.incorrectAnswers, item.answer];
        multipleChoices[i].sort(() => Math.random() - 0.5);
        i++;
      }
    });
    const tmp = quiz;
    quiz = undefined;
    quiz = tmp;
    students = await fetch("/api/v1/student", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
    answers = quizResult.answers;
    student = quizResult.student;
    comment = quizResult.comment;
  });

  onMount(async () => {});

  async function updateQuizResults() {
    await fetch("/api/v1/quizResult/check", {
      headers: getDefaultHeaders(),
      method: "PUT",
      body: JSON.stringify({
        id: quizResult.id,
        quiz: {
          id: quiz.id,
        },
        student: {
          id: student.id,
        },
        answers,
        comment,
      }),
    }).then(async () => await dispatch("refreshQuizResults"));
  }
</script>

{quiz?.title} ({quiz?.subject.name}) by {quiz?.teacher.userInfo.user.name}<br />
Student:
<select bind:value={student}>
  <option value="">Select a Student</option>
  {#each students as student}
    <option value={student}>{student.userInfo.user.name}</option>
  {/each}
</select>
<br />
{#if quiz?.items.length > 0}
  {#each quiz?.items as item, i}
    {i + 1}.&nbsp;{item.question}<br />
    {#if item.type === "Multiple choices" && multipleChoices[i]?.length > 0}
      <div style="display: flex; flex-wrap: wrap;" class="m-3">
        {#each multipleChoices[i] as answer}
          &nbsp;&nbsp;<label class="form-check-label m-3">
            <input
              class="form-check-input"
              bind:group={answers[i]}
              type="radio"
              value={answer}
            />{answer}</label
          >
        {/each}
      </div>
    {:else if item.type === "True or False"}
      <div style="display: flex; flex-wrap: wrap;" class="m-3">
        &nbsp;&nbsp;<label class="form-check-label m-3">
          <input
            class="form-check-input m-3"
            bind:group={answers[i]}
            type="radio"
            value="true"
          />True</label
        >
        &nbsp;&nbsp;<label class="form-check-label m-3">
          <input
            class="form-check-input"
            bind:group={answers[i]}
            type="radio"
            value="false"
          />False</label
        >
      </div>
    {:else if item.type === "Fill in the blank"}
      &nbsp;&nbsp;Answer:<input id={`${i}Input`} bind:value={answers[i]} />
    {/if}
  {/each}
{/if}
<br />
Comment:
<input type="text" id="commentInput" bind:value={comment} />
<br />
<button on:click={async () => await updateQuizResults()}>Submit</button>
