<script>
  import ActivityTable from "./Activity/ActivityTable.svelte";
  import TakeActivity from "./ActivityResult/TakeActivity.svelte";
  import Container from "./shared/Container.svelte";
  let activityTable;
  let selectedActivity;

  async function onSelectActivity(e) {
    selectedActivity = e.detail;
  }
</script>

<ActivityTable bind:this={activityTable} on:selectActivity={onSelectActivity} />
<br />
{#if selectedActivity != undefined}
  <TakeActivity activity={selectedActivity} on:refreshActivityResults />
{/if}
