<script>
  import { createEventDispatcher, onMount } from "svelte";
  import FullCalendar from "svelte-fullcalendar";
  import dayGridPlugin from "@fullcalendar/daygrid";
  import {
    getCurrentUserInfo,
    getDecodedToken,
    getDefaultHeaders,
  } from "../utils.js";
  let options;

  const dispatch = createEventDispatcher();

  onMount(async () => {
    let events = [];
    (await loadCourse()).forEach((course) => {
      events.push({
        title: `${course.filename ? "Activity" : "Quiz"} | ${course.title} by ${
          course.teacher.userInfo.user.name
        }`,
        date: course.dateCreated,
      });
    });

    options = {
      dateClick: (event) => alert("date click! " + event.dateStr),
      events,
      plugins: [dayGridPlugin],
    };
  });

  const loadCourse = async () => {
    let sortedCourse = [];
    let quizzes = [];
    let activities = [];
    quizzes = await fetch(`/api/v1/quiz`, {
      headers: getDefaultHeaders(),
    })
      .then((r) => r.json())
      .catch((err) => (quizzes = []));
    activities = await fetch(`/api/v1/activity`, {
      headers: getDefaultHeaders(),
    })
      .then((r) => r.json())
      .catch((err) => (activities = []));
    quizzes.forEach((quiz) => {
      sortedCourse.push(quiz);
    });
    activities.forEach((activity) => {
      sortedCourse.push(activity);
    });
    sortedCourse = sortedCourse.sort((a, b) => {
      return new Date(a.dateUploaded ?? a.dateCreated) >
        new Date(b.dateUploaded ?? b.dateCreated)
        ? -1
        : 1;
    });
    return sortedCourse;
  };
</script>

