<script>
  import { createEventDispatcher, onMount } from "svelte";
  import {
    getCurrentUserInfo,
    formatDate,
    getDefaultHeaders,
  } from "./utils.js";
  const dispatch = new createEventDispatcher();
  import Card from "./shared/Card.svelte";
  import Modal from "./shared/Modal.svelte";
  import Cookies from "js-cookie";

  let subjects = [];
  let quizzes = [];
  let learningMaterials = [];
  let activities = [];
  let courseModal;
  let selectedSubject;
  let courses = [];
  let students = [];
  let student;
  let ps = [];

  onMount(async () => {
    students = await fetch("/api/v1/student", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());

    students.forEach((s) => {
      if (s.userInfo.id === getCurrentUserInfo().id) {
        student = s;
      }
    });
    subjects = await fetch("/api/v1/subject", {
      headers: getDefaultHeaders(),
    })
      .then((r) => r.json())
      .then(async (ss) => {
        ss.forEach(async (s) => {
          let p = await getProgress(s);
          let tmp = ps;
          tmp.push(p);
          ps = [];
          ps = tmp;
        });
        return ss;
      });
  });

  const getProgress = async (subject) => {
    let progress;

    if (subject.id !== undefined && student?.id !== undefined) {
      progress = await fetch(
        `/api/v1/quizResult/progress/studentId/${student.id}/subjectId/${subject.id}`,
        {
          headers: getDefaultHeaders(),
        }
      ).then((r) => r.json());
    }

    return progress?.value?.toFixed(2) ?? 0.0;
  };

  const openCourse = async (subject) => {
    selectedSubject = subject;
    courses = await loadCourse();
    await courseModal.toggle();
  };

  const loadCourse = async () => {
    let sortedCourse = [];
    quizzes = await fetch(`/api/v1/quiz/subjectId/${selectedSubject.id}`, {
      headers: getDefaultHeaders(),
    })
      .then((r) => r.json())
      .catch((err) => (quizzes = []));
    learningMaterials = await fetch(
      `/api/v1/learningMaterial/subjectId/${selectedSubject.id}`,
      {
        headers: getDefaultHeaders(),
      }
    )
      .then((r) => r.json())
      .catch((err) => (learningMaterials = []));
    activities = await fetch(
      `/api/v1/activity/subjectId/${selectedSubject.id}`,
      {
        headers: getDefaultHeaders(),
      }
    )
      .then((r) => r.json())
      .catch((err) => (activities = []));
    quizzes.forEach((quiz) => {
      sortedCourse.push(quiz);
    });
    learningMaterials.forEach((learningMaterial) => {
      sortedCourse.push(learningMaterial);
    });
    activities.forEach((activity) => {
      sortedCourse.push(activity);
    });
    sortedCourse = sortedCourse.sort((a, b) => {
      return new Date(a.dateUploaded ?? a.dateCreated) >
        new Date(b.dateUploaded ?? b.dateCreated)
        ? -1
        : 1;
    });
    return sortedCourse;
  };

  async function downloadFile(learningMaterial) {
    await fetch(
      `/api/v1/learningMaterial/${learningMaterial.id}/downloadFile`,
      {
        headers: {
          Authorization: `Bearer ${Cookies.get("token")}`,
        },
      }
    ).then(async (r) => {
      if (r.status === 200) {
        let b = await r.blob();
        let objectUrl = window.URL.createObjectURL(b);
        let anchor = document.createElement("a");
        anchor.href = objectUrl;
        anchor.download = learningMaterial.filename;
        anchor.click();
        window.URL.revokeObjectURL(objectUrl);
      }
    });
  }

  function secondsToHms(d) {
    d = Number(d);
    var h = Math.floor(d / 3600);
    var m = Math.floor((d % 3600) / 60);
    var s = Math.floor((d % 3600) % 60);

    var hDisplay = h > 0 ? h + (h == 1 ? " hour, " : " hours, ") : "";
    var mDisplay = m > 0 ? m + (m == 1 ? " minute, " : " minutes, ") : "";
    var sDisplay = s > 0 ? s + (s == 1 ? " second" : " seconds") : "";
    return hDisplay + mDisplay + sDisplay;
  }
</script>

<Modal bind:this={courseModal}>
  <div slot="body">
    {#each courses as course}
      <Card
        title={`${
          course.dateCreated
            ? course.filename
              ? "Activity"
              : "Quiz"
            : "Learning Material"
        } | ${course.title} by ${course.teacher.userInfo.user.name}`}
      >
        <h6>{formatDate(course.dateCreated ?? course.dateUploaded)}</h6>
        {#if course.dateUploaded}
          <h3>
            {course.description}
          </h3>
          <iframe
            class="doc"
            title="preview"
            src={`https://docs.google.com/gview?url=${course.filename}&embedded=true`}
          />
          <button
            class="btn btn-info"
            on:click={async () => await downloadFile(course)}
          >
            Download {course.filename}
          </button>
        {:else if course.filename && course.dateCreated}
          <iframe
            class="doc"
            title="preview"
            src={`https://docs.google.com/gview?url=${course.filename}&embedded=true`}
          />
          <button
            class="btn btn-info"
            on:click={async () => await downloadFile(course)}
          >
            Download {course.filename}
          </button>
          <h5>
            Date of submission:
            {formatDate(course.dateOfSubmission)}
            {new Date(course.dateOfSubmission) <= new Date()
              ? "Ended"
              : "Started"}
          </h5>
        {:else}
          <h5>
            Date of submission:
            {formatDate(course.dateOfSubmission)}
            {new Date(course.dateOfSubmission) <= new Date()
              ? "Ended"
              : "Started"}
          </h5>
          <h5>Time limit: {secondsToHms(course.timeLimit)}</h5>
        {/if}
      </Card>
    {/each}
  </div>
</Modal>

<div class="row">
  {#each subjects as subject, i}
    <div
      class="col-12 col-md-6 col-lg-4"
      on:click={async () => await openCourse(subject)}
    >
      <Card
        image={`https://via.placeholder.com/150x200.png?text=${subject.name}`}
        title={subject.name}
      >
        {#if getCurrentUserInfo().user.role == "Student"}
          <ul class="list-group list-group-flush">
            <li class="list-group-item px-4 pb-4">
              <p class="mb-2 font-weight-bold">
                Progress <span class="float-end">{ps[i]}%</span>
              </p>
              <div class="progress progress-sm">
                <div
                  class="progress-bar"
                  role="progressbar"
                  aria-valuenow={ps[i]}
                  aria-valuemin="0"
                  aria-valuemax="100"
                  style="width: 100%;"
                />
              </div>
            </li>
          </ul>
        {/if}
      </Card>
    </div>
  {/each}
</div>
