<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js";
  import Cookies from "js-cookie";
  export let activity;
  const dispatch = new createEventDispatcher();
  let students = [];
  let student;
  let isStudent;
  let files = [];

  onMount(async () => {
    let i = 0;
    const tmp = activity;
    activity = undefined;
    activity = tmp;
    students = await fetch("/api/v1/student", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());

    let userInfo = getCurrentUserInfo();

    if (userInfo?.user?.role == "Student") {
      student = students.filter(
        (s) => s.userInfo.user.id == userInfo.user.id
      )[0];
      isStudent = true;
    }
  });

  async function checkActivity() {
    const formData = new FormData();
    formData.append("activityId", activity.id);
    formData.append("studentId", student.id);
    formData.append("file", files[0]);
    await fetch("/api/v1/activityResult/uploadFile", {
      headers: {
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "POST",
      body: formData,
    }).then(async () => await dispatch("refreshActivityResults"));
  }
</script>

{#if activity != undefined}
  {activity.title} ({activity.subject.name}) by {activity.teacher.userInfo.user
    .name}<br />
  {#if !isStudent}
    Student:
    <div class="mb-3 row">
      <select class="form-control" bind:value={student}>
        <option value="">Select a Student</option>
        {#each students as student}
          <option value={student}>{student.userInfo.user.name}</option>
        {/each}
      </select>
    </div>
    <br />
  {/if}
  <div class="mb-3 row">
    <label
      >File: <input
        class="form-control"
        type="file"
        name="file"
        bind:files
      /></label
    >
  </div>
  <br />
  <div class="modal-footer ">
    <button class="btn btn-success" on:click={async () => await checkActivity()}
      >Submit</button
    >
  </div>
{/if}
