<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getDefaultHeaders, formatDate } from "../utils.js";
  const dispatch = new createEventDispatcher();
  export let canSelect = true;
  export let canEdit;
  export let canDelete;
  let quizzes = [];

  onMount(async () => {
    await refreshQuizzes();
  });

  export async function refreshQuizzes() {
    quizzes = await fetch("/api/v1/quiz", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
  }

  function secondsToHms(d) {
    d = Number(d);
    var h = Math.floor(d / 3600);
    var m = Math.floor((d % 3600) / 60);
    var s = Math.floor((d % 3600) % 60);

    var hDisplay = h > 0 ? h + (h == 1 ? " hour, " : " hours, ") : "";
    var mDisplay = m > 0 ? m + (m == 1 ? " minute, " : " minutes, ") : "";
    var sDisplay = s > 0 ? s + (s == 1 ? " second" : " seconds") : "";
    return hDisplay + mDisplay + sDisplay;
  }
</script>

{#if quizzes.length > 0}
  <table class="table table-striped">
    <thead>
      <tr>
        <th>Title</th>
        <th>Teacher</th>
        <th>Subject</th>
        <th>Time limit (seconds)</th>
        <th>Deadline</th>
        <th>Actions</th>
      </tr>
    </thead>
    <tbody>
      {#each quizzes as quiz}
        <tr>
          <td>{quiz.title}</td>
          <td>{quiz.teacher.userInfo.user.name}</td>
          <td>{quiz.subject.name}</td>
          <td>{secondsToHms(quiz.timeLimit)}</td>
          <td
            >{formatDate(quiz.dateOfSubmission)}
            {new Date(quiz.dateOfSubmission) <= new Date()
              ? "Ended"
              : "Started"}</td
          >
          <td>
            {#if canSelect}
              <button
                class="btn btn-primary"
                disabled={new Date(quiz.dateOfSubmission) <= new Date()}
                on:click={async () => await dispatch("selectQuiz", quiz)}
                >Select</button
              >
            {/if}
            {#if canEdit}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () => await dispatch("editQuiz", quiz)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-edit-2 align-middle"
                  ><path
                    d="M17 3a2.828 2.828 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"
                  /></svg
                ></button
              >
            {/if}
            {#if canDelete}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () => await dispatch("deleteQuiz", quiz)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-trash align-middle"
                  ><polyline points="3 6 5 6 21 6" /><path
                    d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                  /></svg
                ></button
              >
            {/if}
          </td>
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
