<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js";
  const dispatch = new createEventDispatcher();

  export let teacher;
  let username;
  let password;
  let teacherName;
  let birthday;
  let gender;
  let address;
  let contactNumber;

  onMount(async () => {
    username = teacher.userInfo.user.username;
    teacherName = teacher.userInfo.user.name;
    role = teacher.userInfo.user.role;
    birthday = teacher.userInfo.birthday;
    gender = teacher.userInfo.gender;
    address = teacher.userInfo.address;
    contactNumber = teacher.userInfo.contactNumber;
  });

  async function updateTeachers() {
    await fetch("/api/v1/teacher", {
      headers: getDefaultHeaders(),
      method: "PUT",
      body: JSON.stringify({
        id: teacher.id,
        userInfo: {
          id: teacher.userInfo.id,
          user: {
            id: teacher.userInfo.user.id,
            username,
            password,
            name: teacherName,
          },
          birthday: new Date(birthday).toISOString(),
          gender,
          address,
          contactNumber,
        },
      }),
    }).then(async () => await dispatch("refreshTeachers"));
  }
</script>

<div class="mb-3 row">
  <label
    >Username: <input
      class="form-control"
      type="text"
      bind:value={username}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Password: <input
      class="form-control"
      type="password"
      bind:value={password}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Name: <input
      class="form-control"
      type="text"
      bind:value={teacherName}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Birthday: <input
      class="form-control"
      type="datetime-local"
      bind:value={birthday}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Gender:
    <select class="form-control" bind:value={gender}>
      <option value="">Select gender</option>
      <option>Male</option>
      <option>Female</option>
    </select>
  </label>
</div>
<div class="mb-3 row">
  <label
    >Address: <input
      class="form-control"
      type="text"
      bind:value={address}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Contact Number: <input
      class="form-control"
      type="tel"
      placeholder="+639123456789"
      pattern="\+639[0-9]{9}"
      bind:value={contactNumber}
    /></label
  >
</div>
<div class="modal-footer ">
  <button class="btn btn-warning" on:click={async () => await updateTeachers()}
    >Submit</button
  >
</div>
