<script>
  import { onMount, createEventDispatcher } from "svelte";
  import Cookies from "js-cookie";
  import {
    getCurrentUserInfo,
    getDefaultHeaders,
    formatDate,
  } from "../utils.js";
  const dispatch = new createEventDispatcher();
  export let canSelect = true;
  export let canEdit;
  export let canDelete;
  let activities = [];
  const currentUserInfo = getCurrentUserInfo();

  onMount(async () => {
    await refreshActivities();
  });

  export async function refreshActivities() {
    activities = await fetch("/api/v1/activity", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
  }

  async function downloadFile(activity) {
    await fetch(`/api/v1/activity/${activity.id}/downloadFile`, {
      headers: {
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then(async (r) => {
      if (r.status === 200) {
        let b = await r.blob();
        let objectUrl = window.URL.createObjectURL(b);
        let anchor = document.createElement("a");
        anchor.href = objectUrl;
        anchor.download = activity.filename;
        anchor.click();
        window.URL.revokeObjectURL(objectUrl);
      }
    });
  }
</script>

{#if activities.length > 0}
  <table class="table table-striped">
    <thead>
      <tr>
        <th>Title</th>
        {#if currentUserInfo.user?.role == "Admin"}
          <th>Teacher</th>
        {/if}
        <th>Subject</th>
        <th>Filename</th>
        <th>Deadline</th>
        <th>Actions</th>
      </tr>
    </thead>
    <tbody>
      {#each activities as activity}
        <tr>
          <td>{activity.title}</td>
          {#if currentUserInfo.user?.role == "Admin"}
            <td>{activity.teacher.userInfo.user.name}</td>
          {/if}
          <td>{activity.subject.name}</td>
          <td>
            <button
              class="btn btn-info"
              on:click={async () => await downloadFile(activity)}
            >
              Download {activity.filename}
            </button>
          </td>
          <td
            >{formatDate(activity.dateOfSubmission)}
            {new Date(activity.dateOfSubmission) <= new Date()
              ? "Ended"
              : "Started"}</td
          >
          <td>
            {#if canSelect}
              <button
                class="btn btn-info"
                on:click={async () =>
                  await dispatch("selectActivity", activity)}>Select</button
              >
            {/if}
            {#if canEdit}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () => await dispatch("editActivity", activity)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-edit-2 align-middle"
                  ><path
                    d="M17 3a2.828 2.828 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"
                  /></svg
                ></button
              >
            {/if}
            {#if canDelete}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () =>
                  await dispatch("deleteActivity", activity)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-trash align-middle"
                  ><polyline points="3 6 5 6 21 6" /><path
                    d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                  /></svg
                ></button
              >
            {/if}
          </td>
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
