<script>
  import { createEventDispatcher } from "svelte";
  import AddActivity from "./Activity/AddActivity.svelte";
  import EditActivity from "./Activity/EditActivity.svelte";
  import ActivityTable from "./Activity/ActivityTable.svelte";
  import Container from "./shared/Container.svelte";
  import Modal from "./shared/Modal.svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "./utils.js";
  const dispatch = new createEventDispatcher();
  let activityTable;
  let addActivityModal;
  let editActivityModal;
  let deleteActivityModal;
  let selectedActivity;

  async function onRefreshActivities() {
    await activityTable.refreshActivities();
    await addActivityModal.close();
    await editActivityModal.close();
    await deleteActivityModal.close();
  }

  async function onEditActivity(e) {
    selectedActivity = e.detail;
    await editActivityModal.toggle();
  }

  async function onDeleteActivity(e) {
    selectedActivity = e.detail;
    await deleteActivityModal.toggle();
  }

  async function deleteActivity() {
    await fetch(`/api/v1/activity/${selectedActivity.id}`, {
      headers: getDefaultHeaders(),
      method: "DELETE",
    }).then(async () => await onRefreshActivities());
  }
</script>

<button
  class="btn btn-primary"
  on:click={async () => await addActivityModal.toggle()}>New Activity</button
>
<Modal bind:this={addActivityModal}>
  <AddActivity slot="body" on:refreshActivities={onRefreshActivities} />
</Modal>
<br />
<ActivityTable
  bind:this={activityTable}
  on:editActivity={onEditActivity}
  on:deleteActivity={onDeleteActivity}
  canSelect={false}
  canEdit={true}
  canDelete={true}
/>
<Modal bind:this={editActivityModal}>
  <EditActivity
    slot="body"
    activity={selectedActivity}
    on:refreshActivities={onRefreshActivities}
  />
</Modal>
<Modal bind:this={deleteActivityModal}>
  <span slot="body" class="m-3">
    Are you Sure you want to delete activity: "{selectedActivity.title}" by {selectedActivity
      .teacher.userInfo.user.name}?
  </span>
  <button
    slot="footer"
    class="btn btn-danger"
    on:click={async () => await deleteActivity()}>Delete</button
  >
</Modal>
