<script>
  import Container from "./shared/Container.svelte";
  import ActivityResultTable from "./ActivityResult/ActivityResultTable.svelte";
  import { createEventDispatcher, onMount } from "svelte";
  import EditActivityResult from "./ActivityResult/EditActivityResult.svelte";
  import Modal from "./shared/Modal.svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "./utils.js";
  const dispatch = new createEventDispatcher();
  let activityResultTable;
  let editActivityResultModal;
  let deleteActivityResultModal;
  let selectedActivityResult;

  async function onRefreshActivityResults() {
    await activityResultTable.refreshActivityResults();
    await editActivityResultModal.close();
    await deleteActivityResultModal.close();
  }

  async function onEditActivityResult(e) {
    selectedActivityResult = e.detail;
    await editActivityResultModal.toggle();
  }

  async function onDeleteActivityResult(e) {
    selectedActivityResult = e.detail;
    await deleteActivityResultModal.toggle();
  }

  async function deleteActivityResult() {
    await fetch(`/api/v1/activityResult/${selectedActivityResult.id}`, {
      headers: getDefaultHeaders(),
      method: "DELETE",
    }).then(async () => await onRefreshActivityResults());
  }
</script>

<ActivityResultTable
  bind:this={activityResultTable}
  on:editActivityResult={onEditActivityResult}
  on:deleteActivityResult={onDeleteActivityResult}
  canSelect={false}
  canEdit={true}
  canDelete={true}
/>
<Modal bind:this={editActivityResultModal}>
  <EditActivityResult
    slot="body"
    activityResult={selectedActivityResult}
    on:refreshActivityResults={onRefreshActivityResults}
  />
</Modal>
<Modal bind:this={deleteActivityResultModal}>
  <span slot="body" class="m-3">
    Are you Sure you want to delete activity result: "{selectedActivityResult
      .activity.title}" answered by "{selectedActivityResult.student.userInfo
      .user.name}"?
  </span>
  <button
    slot="footer"
    class="btn btn-danger"
    on:click={async () => await deleteActivityResult()}>Delete</button
  >
</Modal>
