<script>
  import ActivityResultTable from "./ActivityResult/ActivityResultTable.svelte";
  import CheckActivity from "./ActivityResult/CheckActivity.svelte";
  let activityResultsTable;
  let selectedActivityResult;

  async function onSelectActivityResult(e) {
    selectedActivityResult = e.detail;
  }
</script>

<ActivityResultTable
  bind:this={activityResultsTable}
  on:selectActivityResult={onSelectActivityResult}
/>
<br />
{#if selectedActivityResult != undefined}
  <CheckActivity
    activityResult={selectedActivityResult}
    on:refreshActivityResults
  />
{/if}
